<template>
  <div class="auth-container">
    <div class="auth-card">
      <div class="auth-header">
        <span class="material-symbols-rounded">person_add</span>
        <h1>Create Account</h1>
        <p class="subtitle">Join to start collecting</p>
      </div>

      <form @submit.prevent="handleSubmit">
        <div class="form-group">
          <div class="input-wrapper">
            <span class="material-symbols-rounded">person</span>
            <input 
              v-model="formData.firstName"
              type="text"
              placeholder="First Name"
              required
            />
          </div>
        </div>

        <div class="form-group">
          <div class="input-wrapper">
            <span class="material-symbols-rounded">badge</span>
            <input 
              v-model="formData.lastName"
              type="text"
              placeholder="Last Name"
              required
            />
          </div>
        </div>

        <div class="form-group">
          <div class="input-wrapper">
            <span class="material-symbols-rounded">mail</span>
            <input 
              v-model="formData.email"
              type="email"
              placeholder="Email address"
              required
            />
          </div>
        </div>
        
        <div class="form-group">
          <div class="input-wrapper">
            <span class="material-symbols-rounded">lock</span>
            <input 
              v-model="formData.password"
              type="password"
              placeholder="Password"
              required
            />
          </div>
        </div>

        <div v-if="error" class="error-message">
          <span class="material-symbols-rounded">error</span>
          {{ error }}
        </div>

        <button type="submit" :disabled="loading" class="submit-button" :class="{ loading }">
          <span class="button-content">
            <span class="material-symbols-rounded">person_add</span>
            {{ loading ? 'Creating account...' : 'Create Account' }}
          </span>
        </button>
      </form>

      <div class="auth-footer">
        <p>Already have an account?</p>
        <router-link to="/login" class="auth-link">
          Sign In
          <span class="material-symbols-rounded">arrow_forward</span>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import auth from '@/store/auth'
import { ref } from 'vue'
import { supabase } from '@/services/supabase'

export default {
  name: 'Register',
  setup() {
    const formData = ref({
      firstName: '',
      lastName: '',
      email: '',
      password: ''
    })
    const loading = ref(false)
    const error = ref(null)

    const handleSubmit = async () => {
      loading.value = true
      error.value = null
      
      try {
        const { user, error: signUpError } = await auth.signUp(formData.value)
        
        if (signUpError) throw signUpError

        // Update profile with names
        const { error: updateError } = await supabase
          .from('profiles')
          .update({
            first_name: formData.value.firstName,
            last_name: formData.value.lastName,
            username: `${formData.value.firstName} ${formData.value.lastName}`
          })
          .eq('id', user.id)

        if (updateError) throw updateError

        // Redirect to login
        router.push('/login')
      } catch (err) {
        error.value = err.message
      } finally {
        loading.value = false
      }
    }

    return {
      formData,
      loading,
      error,
      handleSubmit
    }
  }
}
</script>

<style scoped>
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  background: var(--background-primary);
}

.auth-card {
  background: var(--card-background);
  padding: 32px;
  border-radius: 20px;
  width: 100%;
  max-width: 400px;
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--card-border);
}

.auth-header {
  text-align: center;
  margin-bottom: 32px;
}

.auth-header .material-symbols-rounded {
  font-size: 48px;
  color: var(--accent-primary);
  margin-bottom: 16px;
}

.auth-header h1 {
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 8px 0;
  color: var(--text-primary);
}

.subtitle {
  color: var(--text-secondary);
  margin: 0;
  font-size: 14px;
}

.form-group {
  margin-bottom: 16px;
}

.input-wrapper {
  display: flex;
  align-items: center;
  background: var(--surface-background);
  border-radius: 12px;
  padding: 12px 16px;
  transition: all 0.2s ease;
}

.input-wrapper:focus-within {
  background: var(--background-elevated);
  box-shadow: 0 0 0 2px var(--accent-primary);
}

.input-wrapper .material-symbols-rounded {
  color: var(--text-secondary);
  margin-right: 12px;
}

input {
  width: 100%;
  background: none;
  border: none;
  outline: none;
  color: var(--text-primary);
  font-size: 15px;
}

input::placeholder {
  color: var(--text-secondary);
}

.error-message {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px;
  background: var(--system-red);
  color: white;
  border-radius: 8px;
  margin-bottom: 16px;
  font-size: 14px;
}

.submit-button {
  width: 100%;
  padding: 14px;
  background: var(--accent-primary);
  color: white;
  border: none;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.button-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.submit-button:hover {
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.submit-button:active {
  transform: translateY(0);
}

.submit-button.loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.auth-footer {
  margin-top: 32px;
  text-align: center;
}

.auth-footer p {
  color: var(--text-secondary);
  margin: 0 0 8px 0;
}

.auth-link {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  color: var(--accent-primary);
  text-decoration: none;
  font-weight: 500;
}

.auth-link:hover {
  text-decoration: underline;
}

@media (max-width: 480px) {
  .auth-card {
    padding: 24px;
  }
}
</style>
